import request from '@/utils/request'

export function getAllRules() {
  return request.rules({
    url: '',
    method: 'get'
  })
}

export function getSinRules(list) {
  return request.rules({
    url: `/${list}`,
    method: 'get'
  })
}

export function addSinRules(list, params) {
  return request.rules({
    url: `/${list}`,
    method: 'post',
    data: params
  })
}

export function delSinRules(list, params) {
  return request.rules({
    url: `/${list}`,
    method: 'delete',
    data: params
  })
}

export function updateSinRules(list, params) {
  return request.rules({
    url: `/${list}`,
    method: 'put',
    data: params
  })
}

export function getSecondTitle(ruleName) {
  return request.rules({
    url: '',
    method: 'post',
    data: ruleName
  })
}



