<template>
  <div>
    <el-table class="el-table" highlight-current-row :data="rulesNameItem">
      <el-table-column fixed label="名称">
        <template slot-scope="scope">
          <el-input size="small" @focus="getOldVal(scope.row.data)"
                    @change="getNewVal(scope.row.data)" v-model="scope.row.data"
                    placeholder="请输入内容"></el-input>
          <span>{{scope.row.data}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <template v-if="scope.row.status === true">
            <el-tooltip class="item" effect="dark" content="取消" placement="top">
              <el-button @click="handleCelSinRules" type="text" size="small">
                <i class="iconfont icon-setting">&#xe65c;</i>
              </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="确定" placement="top">
              <el-button @click="handleAddSinRules" type="text" size="small">
                <i class="iconfont icon-setting">&#xe60a;</i>
              </el-button>
            </el-tooltip>
          </template>
          <template v-else>
            <!--删除和删除提示-->
            <el-tooltip class="item" effect="dark" content="删除" placement="top">
              <el-button @click="handleDel(scope.row.data)" type="text" size="small">
                <i class="iconfont icon-setting">&#xe663;</i>
              </el-button>
            </el-tooltip>
            <el-dialog title="提示" :visible.sync="delDialogVisible"
                       width="30%">
              <span>确定删除？</span>
              <span slot="footer" class="dialog-footer">
              <el-button @click="delDialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="handleOk">确 定</el-button>
              </span>
            </el-dialog>
            <!--保存和修改提示-->
            <el-tooltip class="item" effect="dark" content="保存" placement="top">
              <el-button @click="saveDialogVisible = true" type="text" size="small">
                <i class="iconfont icon-setting">&#xe62d;</i>
              </el-button>
            </el-tooltip>
            <el-dialog title="提示" :visible.sync="saveDialogVisible"
                       width="30%">
              <span>确定修改？</span>
              <span slot="footer" class="dialog-footer">
    <el-button @click="saveDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="handleSave">确 定</el-button>
  </span>
            </el-dialog>
          </template>
        </template>
      </el-table-column>
    </el-table>
    <el-tooltip class="item" effect="dark" content="添加" placement="top">
      <el-button class="add-btn" @click="handleRulesItem" type="primary"
                 size="small" icon="el-icon-plus" circle></el-button>
    </el-tooltip>
  </div>
</template>

<script>
  import * as apiRules from '@/api/monitorRules'

  const list = {
    status: true,
    data: ''
  }
  export default {
    props: ['rulesNameItem', 'tab'],
    data() {
      return {
        itemNewName: '',
        itemOldName: '',
        delDialogVisible: false,
        saveDialogVisible: false
      }
    },
    computed: {
      tabName() {
        return this.tab || 'unwritable_files'
      }
    },
    methods: {
      getNewVal(newVal) {
        this.itemNewName = newVal
      },
      getOldVal(oldVal) {
        this.itemOldName = oldVal
      },
      handleSave() {
        this.saveDialogVisible = false
        apiRules.updateSinRules(this.tabName, {
          OldFile: this.itemOldName,
          NewFile: this.itemNewName
        })
      },
      reloadSingleRules() {
        apiRules.getSinRules(this.tabName).then(res => {
          this.rulesNameItem = res.data.items || []
        })
      },
      // 删除事件：写在对话提示框中出现删除下一个的bug（或许是因为获取了下一个的val）
      handleDel(val) {
        this.delDialogVisible = true
        console.log('val', val)
        apiRules.delSinRules(this.tabName, {
          OldFile: val
        })
        setTimeout(this.reloadSingleRules, 5000)
      },
      // 删除提示tip
      handleOk() {
        this.delDialogVisible = false
      },
      // 添加规则
      handleAddSinRules() {
        apiRules.addSinRules(this.tabName, {
          NewFile: list.data
        })
        list.data = ''
        setTimeout(this.reloadSingleRules, 3000)
      },
      // 添加事件，数组添加尾项
      handleRulesItem() {
        this.rulesNameItem.push(list)
      },
      // 取消事件，将数组尾项删去
      handleCelSinRules() {
        this.rulesNameItem.pop()
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .el-table
    font-size 13px

  .el-table .el-input
    display: none

  .el-table .current-row .el-input
    display: block

  .el-table .current-row .el-input + span
    display: none

  .add-btn
    margin-top 10px
    margin-left 45%
</style>



