<template>
  <a>
    <el-tooltip class="item" effect="dark" content="备份/恢复" placement="top">
      <el-button @click="handleBackupQuertAll" size="small"
                 type="text" class="backup-restore-btn">
        <i class="iconfont icon-setting">&#xe60c;
        </i>
      </el-button>
    </el-tooltip>
    <el-dialog title="备份/恢复" :visible.sync="outerVisible">
      <el-dialog width="30%" title="备份"
                 :visible.sync="innerVisible" append-to-body>
        请填入创建备份的文件名:
        <el-input v-model="backupFileName"></el-input>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="handleBackupCreate">创建备份</el-button>
        </div>
      </el-dialog>
      <backup-table :backupQuertAllList="backupQuertAllList"></backup-table>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="innerVisible = true">
          备份
        </el-button>
      </div>
    </el-dialog>
    <!--<p :class="{'backup-ProgressStatus':isProgress}">文件{{backupFileName}}正在备份</p>-->
    <!--<p :class="{'backup-FinishStatus':isFinish}">文件{{backupFileName}}备份完成</p>-->
  </a>
</template>

<script>
  import * as apiBackup from '@/api/monitorBackup'

  import BackupTable from './backupTable/index'

  export default {
    components: {
      BackupTable
    },
    data() {
      return {
        outerVisible: false,
        innerVisible: false,
        backupFileName: '',
        backupQuertAllList: '',
        isProgress: false,
        isFinish: false
      }
    },
    methods: {
      handleBackupCreate() {
        apiBackup.backupCreate(this.backupFileName).then(res => {
          this.isProgress = true
          if (res.data.accepted === true) {
            this.isFinish = true
          }
        })
        this.outerVisible = false
        this.innerVisible = false
      },
      handleBackupQuertAll() {
        this.outerVisible = true
        apiBackup.backupQuertAll().then(res => {
          this.backupQuertAllList = res.data
        })
      }
    }
  }
</script>
<style lang="stylus" scoped>
  .backup-restore-btn
    height 33px
    font-size 24px
    float right
    margin-top 17px

  .el-input
    width 370px

  p
    text-align right

  .backup-ProgressStatus
    color blue

  .backup-FinishStatus
    color green

  .icon-backup
    color #5286F7
    font-size 2.5em
</style>



