<template>
  <div>
    <a>规则列表</a>
    <div class="edit-rules">
      <el-tooltip class="item" effect="dark" content="设置" placement="top">
        <el-button @click="handleSettingRules" type="text" size="small" class="setting-btn">
          <i class="iconfont icon-setting">&#xe60e;</i>
        </el-button>
      </el-tooltip>
      <el-dialog title="规则设置" :visible.sync="dialogVisible"
                 width="50%" :before-close="handleClose">
        <el-tabs v-model="activeName" type="card">
          <el-tab-pane label="文件系统" name="documentName">
            <el-tabs v-model="documentName" @tab-click="handleGetSinRules">
              <el-tab-pane label="不可写文件" value="unwritable_files" name="unwritable_files">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="不可读文件" value="unread_files" name="unread_files">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="不可删除文件" value="undelete_files" name="undelete_files">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="不可重命名文件" value="unrename_files" name="unrename_files">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="不可创建文件目录" value="uncreated_dirs" name="uncreated_dirs">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
            </el-tabs>
          </el-tab-pane>
          <el-tab-pane label="容器" name="containerName">
            <el-tabs v-model="containerName" @tab-click="handleGetSinRules">
              <el-tab-pane label="连接K8S服务器" name="first">连接K8S服务器</el-tab-pane>
              <el-tab-pane label="登录容器" value="unlogin_containers" name="unlogin_containers">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
            </el-tabs>
          </el-tab-pane>
          <el-tab-pane label="用户" name="userName">
            <el-tabs v-model="userName" @tab-click="handleGetSinRules">
              <el-tab-pane label="用户切换" name="first">用户切换</el-tab-pane>
            </el-tabs>
          </el-tab-pane>
          <el-tab-pane label="程序" name="procedureName">
            <el-tabs v-model="procedureName" @tab-click="handleGetSinRules">
              <el-tab-pane label="修改程序命名空间" name="first">修改程序命名空间</el-tab-pane>
              <el-tab-pane label="开启新进程" name="second">开启新进程</el-tab-pane>
              <el-tab-pane label="软件包管理更新" value="user_soft_mgmts" name="user_soft_mgmts">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="shell白名单" value="user_trusted_shells" name="user_trusted_shells">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="系统用户交互" name="fifth">系统用户交互</el-tab-pane>
            </el-tabs>
          </el-tab-pane>
          <el-tab-pane label="数据库" name="databaseName">
            <el-tabs v-model="databaseName" @tab-click="handleGetSinRules">
              <el-tab-pane label="连接数据库" name="first">连接数据库</el-tab-pane>
              <el-tab-pane label="使用数据库命令" name="second">使用数据库命令</el-tab-pane>
            </el-tabs>
          </el-tab-pane>
          <el-tab-pane label="网络" name="networkName">
            <el-tabs v-model="networkName" @tab-click="handleGetSinRules">
              <el-tab-pane label="允许访问端口" value="user_container_ports" name="user_container_ports">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="SSH连接" name="second">SSH连接</el-tab-pane>
            </el-tabs>
          </el-tab-pane>
        </el-tabs>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">返回</el-button>
         </span>
      </el-dialog>
    </div>
    <el-table class="el-table" :data="allRulesList"
              @cell-click="handleFirstAndSecondTitle">
      <el-table-column type="index" width="20">
      </el-table-column>
      <el-table-column prop="_source.rulename" label="规则名"
                       show-overflow-tooltip header-align="center" min-width="40">
        <template slot-scope="props">
          <i class="rule-name-class">{{props.row._source.rulename}}</i>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import * as apiRules from '@/api/monitorRules'

  import Table from './rulesTable/index'

  const ruleList = new Map()
    .set('unwritable_files', 'documentName')
    .set('unread_files', 'documentName')
    .set('undelete_files', 'documentName')
    .set('unrename_files', 'documentName')
    .set('uncreated_dirs', 'documentName')
    .set('unlogin_containers', 'containerName')
    .set('user_soft_mgmts', 'procedureName')
    .set('user_trusted_shells', 'procedureName')
    .set('user_container_ports', 'networkName')

  export default {
    components: {
      Table
    },
    data() {
      return {
        allRulesList: '',
        secondTitle: '',
        rulesNameItem: '',
        dialogVisible: false,
        activeName: 'documentName',
        documentName: 'unwritable_files',
        containerName: 'unlogin_containers',
        userName: 'first',
        procedureName: 'first',
        databaseName: 'first',
        networkName: 'first',
        tabName: ''
      }
    },
    methods: {
      // 根据二级标题获得一级标题
      getFirstTitle(secondTitle) {
        return ruleList.get(secondTitle) || ''
      },
      GetSinRules(secondTitle) {
        apiRules.getSinRules(secondTitle).then(res => {
          this.rulesNameItem = res.data.items || []
        })
      },
      // 设置按钮跳转到不可写文件
      handleSettingRules() {
        this.dialogVisible = true
        this.activeName = 'documentName'
        this.documentName = 'unwritable_files'
        this.GetSinRules('unwritable_files')
      },
      // 根据二级标题获取数据
      handleGetSinRules(tab) {
        this.tabName = tab.$attrs.value
        apiRules.getSinRules(tab.$attrs.value).then(res => {
          this.rulesNameItem = res.data.items || []
        })
      },
      // 根据一级标题找到二级标题,并赋值
      getSecondTitle(activeName) {
        switch (activeName) {
          case 'documentName':
            this.documentName = this.secondTitle
            break
          case 'containerName':
            this.containerName = this.secondTitle
            break
          case 'userName':
            this.userName = this.secondTitle
            break
          case 'procedureName':
            this.procedureName = this.secondTitle
            break
          case 'databaseName':
            this.databaseName = this.secondTitle
            break
          default:
            this.networkName = this.secondTitle
        }
      },
      // 表格规则名跳转到对应数据
      handleFirstAndSecondTitle(row) {
        this.dialogVisible = true
        // eslint-disable-next-line
        this.secondTitle = row._id
        this.activeName = this.getFirstTitle(this.secondTitle)
        this.getSecondTitle(this.activeName)
        this.GetSinRules(this.secondTitle)
      },
      // 无法点击弹框之外关闭
      handleClose() {
        this.dialogVisible = true
      }
    },
    // 请求规则名
    created() {
      apiRules.getAllRules().then(allRules => {
        this.allRulesList = allRules.data
      })
    }
  }
</script>

<style lang="stylus" scoped>
  a
    font-family '黑体'
    font-size 15px

  .edit-rules
    margin: -25px 0 0 60px

  .el-table
    width 93%

  .setting-btn
    width 100px
    font-size 14px
    float: right

  .rule-name-class
    cursor pointer
</style>
<style lang="stylus">
  .el-dialog__headerbtn .el-dialog__close
    display none
</style>



