<template>
  <div>
    <el-table :data="backupQuertAllList" style="overflow-y: scroll;height: 300px">
      <el-table-column fixed prop="id" label="id" min-width="180">
      </el-table-column>
      <el-table-column prop="status" label="status" min-width="180">
      </el-table-column>
      <el-table-column prop="start_time" label="start_time" min-width="100">
      </el-table-column>
      <el-table-column prop="end_time" label="end_time" min-width="100">
      </el-table-column>
      <el-table-column fixed="right" label="操作">
        <template slot-scope="scope">
          <el-button @click="handleRestoreCreate(scope.row)"
                     type="text" size="small">恢复
          </el-button>
          <el-button @click="handleBackupDelete(scope.row)"
                     type="text" size="small">删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import * as apiBackup from '@/api/monitorBackup'
  import * as apiRestore from '@/api/monitorRestore'

  export default {
    props: ['backupQuertAllList'],
    methods: {
      handleRestoreCreate(row) {
        apiRestore.restoreCreate(row.id).then(res => {
          console.log(res)
        })
      },
      handleBackupDelete(row) {
        apiBackup.backupDelete(row.id).then(res => {
          console.log(res)
          if (res.data.acknowledged === true) {
            this.$alert('删除成功', '备份删除', {
              confirmButtonText: '确定',
              callback: () => {
                window.reload()
                // apiBackup.backupQuertAll().then(res => {
                //   this.backupQuertAllList = res.data
                // })
              }
            })
          }
        })
      }
    }
  }
</script>



