import request from '../utils/request'

export function restoreCreate(snapName) {
  return request.snapshot({
    url: `/restore/${snapName}`,
    method: 'post'
  })
}

export function restoreQuery(snapName) {
  return request.snapshot({
    url: `/restore/${snapName}`,
    method: 'get'
  })
}



