<template>
  <div>
    <div class="ibox-content">
      <el-table :data="queryResult.querydata.hits" @sort-change="handleSort">
        <el-table-column prop="@timestamp" label="时间" sortable="custom"
                         show-overflow-tooltip align="center" min-width="40">
          <template slot-scope="props">
            <i>{{props.row._source['@timestamp']|timeTrans}}</i>
          </template>
        </el-table-column>
        <el-table-column prop="output_fields.container.id.raw" label="容器ID" sortable="custom"
                         show-overflow-tooltip align="center" min-width="30">
          <template slot-scope="props">
            <i>{{props.row._source.output_fields['container.id']}}</i>
          </template>
        </el-table-column>
        <el-table-column prop="output_fields.container.name.raw" label="容器名称" sortable="custom"
                         align="center" show-overflow-tooltip min-width="40">
          <template slot-scope="props">
            <i>{{props.row._source.output_fields['container.name']}}</i>
          </template>
        </el-table-column>
        <el-table-column prop="priority.raw" label="告警等级" sortable="custom"
                         align="center" min-width="20">
          <template slot-scope="props">
            <i class="icon-font"
               :class="{[props.row._source.priority]:true}">&#xe603;</i>
            <i>{{props.row._source.priority|zhPriority}}</i>
          </template>
        </el-table-column>
        <el-table-column prop="rule.raw" label="规则" sortable="custom"
                         align="center" show-overflow-tooltip min-width="40">
          <template slot-scope="props">
            <i>{{props.row._source.rule}}</i>
          </template>
        </el-table-column>
        <el-table-column prop="_source.output" label="事件描述"
                         show-overflow-tooltip align="center">
          <template slot-scope="props">
            <i>{{props.row._source.output}}</i>
          </template>
        </el-table-column>
        <el-table-column prop="host.name.raw" label="主机名" sortable="custom"
                         align="center" show-overflow-tooltip min-width="40">
          <template slot-scope="props">
            <i>{{props.row._source.beat.hostname}}</i>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  // eslint-disable-next-line
  import {fetchStats} from '@/api/transactionMonitor'

  const priorityList = new Map()
    .set('Emergency', '紧急')
    .set('Alert', '警告')
    .set('Critical', '严重')
    .set('Error', '一般')
    .set('Notice', '提示')
    .set('Warning', '提示')
    .set('Informational', '提示')
  export default {
    props: ['queryResult'],
    data() {
      return {
        sortList: '',
        sort: {
          field: '@timestamp',
          order: 'desc'
        }
      }
    },
    methods: {
      handleSearch() {
        fetchStats(this.queryResult).then(res => {
          this.queryResult.querydata = res.data
        })
      },
      handleSort(val) {
        this.sort.field = val.prop
        this.sort.order = val.order.substr(0, val.order.length - 6)
        this.$emit('sortOrder', this.sort)
        this.handleSearch()
      }
    },
    created() {
      this.handleSort()
    },
    filters: {
      zhPriority(fieldVal) {
        return priorityList.get(fieldVal) || []
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .ibox-content
    text-align: center
    table
      width: 100%
      border-collapse: collapse
      margin-bottom: 40px
      thead
        tr
          background #F7F7F7
          height 50px
          box-shadow 4px 4px 20px #FAFAFA
          th
            font-size 1.5em
            font-weight bold
            color #525252
          th:last-child
            display inline-block
            margin 17px -50px 0 0
      tbody
        tr
          font-size: 1.4em
          color #525252
          height 60px
          td:last-child :hover
            color #3E7AF6
            text-decoration underline

  tr:hover
    background #FAFAFA

  .icon-bottom
    color #548AF8

  .Emergency
    color #CC092F
    font-size 0.7em

  .Alert
    color #F37043
    font-size 0.7em

  .Critical
    color #ECDE29
    font-size 0.7em

  .Error
    color #00A8E1
    font-size 0.7em

  .Notice, .Warning, .Informational
    color #73AE57
    font-size 0.7em

  .icon-sort
    color #6F97E8

  .nowrap
    white-space nowrap

  .icon-change
    color #6F97E8
</style>




