import request from '../utils/request'

export function backupCreate(snapName) {
  return request.snapshot({
    url: `/${snapName}`,
    method: 'post'
  })
}

export function backupDelete(snapName) {
  return request.snapshot({
    url: `/${snapName}`,
    method: 'delete'
  })
}

export function backupQuertAll() {
  return request.snapshot({
    url: '',
    method: 'get'
  })
}



