<template>
  <div>
    <header>
      <navbar></navbar>
    </header>
    <section class="main">
      <p class="main-nav">
        <router-link to="/">
          总览
        </router-link>
        <span>> >告警事件详情</span>
      </p>
      <section class="main-wrapper">
        <div class="brief">
          <el-container>
            <transition>
              <el-aside width="30%" v-if="show">
                <event-level :total="total" :dataList="dataList"
                             :priorityPrecent="priorityPrecent"></event-level>
                <event-details></event-details>
              </el-aside>
            </transition>
            <el-main>
              <div class="alertList">
                <span><i class="iconfont icon-screen">&#xe60d;
                </i>
                <i> 查询日期：</i></span>
                <el-button @click="handleTimeData('today','1')"
                           :class="{history_time_active:isActive==='1'}" class="history_time">
                  今天
                </el-button>
                <el-button @click="handleTimeData('yesterday','2')" class="history_time"
                           :class="{history_time_active:isActive==='2'}">
                  昨天
                </el-button>
                <el-button @click="handleTimeData('week','3')" class="history_time"
                           :class="{history_time_active:isActive==='3'}">
                  近7天
                </el-button>
                <el-button @click="handleTimeData('month','4')" class="history_time"
                           :class="{history_time_active:isActive==='4'}">
                  近30天
                </el-button>
                <el-date-picker @change="getDateArray" v-model="dateArray"
                                type="daterange" range-separator="-" start-placeholder="开始日期"
                                end-placeholder="结束日期" size="mini" class="el-date-picker">
                </el-date-picker>
              </div>
              <div class="securitylists-text">
                <p class="security-search-box">
                  <el-select v-model="dataList.must[1].field"
                             class="el-select" placeholder="请选择">
                    <el-option v-for="item in options" :key="item.value"
                               :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                  <input class="search-text" type="text"
                         placeholder="请输入要过滤的关键字"
                         v-model="dataList.must[1].value">
                  <el-button @click="handleSearch" class="search-btn btn">
                    <i class="iconfont icon-search">&#xe665;
                    </i></el-button>
                </p>
                <BackupAndRestore></BackupAndRestore>
              </div>
              <priority-table @sortOrder="sortOrder"
                              :queryResult="dataList"></priority-table>
              <div class="divblock">
                <el-pagination @size-change="handleSizeChange"
                               @current-change="handleCurrentChange" :page-sizes="[10, 20, 50, 100]"
                               :page-size="10" layout="total, sizes, prev, pager, next, jumper"
                               :total="dataList.querydata.total" background>
                </el-pagination>
              </div>
            </el-main>
          </el-container>
        </div>
      </section>
    </section>
  </div>
</template>
<script type="text/ecmascript-6">
  // eslint-disable-next-line
  import {fetchStats} from '@/api/transactionMonitor'
  import io from 'socket.io-client'
  //
  import Navbar from 'components/navbar'
  import EventLevel from './components/eventLevel'
  import EventDetails from './components/eventDetails/index'
  import BackupAndRestore from './components/backupAndRestore/index'
  import PriorityTable from './components/priorityTable'

  const t = (24 * 60 * 60 * 1000) - 1

  export default {
    name: 'index',
    components: {
      Navbar,
      EventLevel,
      EventDetails,
      BackupAndRestore,
      PriorityTable
    },
    data() {
      return {
        dateArray: '',
        // eslint-disable-next-line
        show: true,
        options: [
          {
            value: '@timestamp',
            label: '日期'
          }, {
            value: 'time',
            label: '时间'
          }, {
            value: 'container.id',
            label: '容器ID'
          }, {
            value: 'container.name',
            label: '容器名称'
          }, {
            value: 'priority',
            label: '告警等级'
          }, {
            value: 'rule',
            label: '规则'
          }, {
            value: 'output',
            label: '事件描述'
          }, {
            value: 'hostName',
            label: '主机名'
          }
        ],
        dataList: {
          must: [
            {
              type: '',
              from: '',
              to: ''
            },
            {
              type: 'match',
              field: 'container.name',
              value: ''
            }
          ],
          pagination: {
            from: 0,
            size: 10
          },
          sort: {
            field: '@timestamp',
            order: 'desc'
          },
          querydata: ''
        },
        currentPage: '',
        total: '',
        isActive: '',
        priorityPrecent: '',
        timer: ''
      }
    },
    computed: {
      todayZeroTime() {
        return (new Date(new Date().toLocaleDateString())).getTime()
      },
      timeList() {
        return {
          today: {
            start: new Date(this.todayZeroTime),
            end: new Date(this.todayZeroTime + t)
          },
          yesterday: {
            start: new Date(this.todayZeroTime - t),
            end: new Date(this.todayZeroTime - 1)
          },
          week: {
            start: new Date(this.todayZeroTime - (7 * t)),
            end: new Date(this.todayZeroTime + t)
          },
          month: {
            start: new Date(this.todayZeroTime - (30 * t)),
            end: new Date(this.todayZeroTime + t)
          }
        }
      }
    },
    methods: {
      // 抓取所有数据
      handleSearch() {
        fetchStats(this.dataList).then(res => {
          this.dataList.querydata = res.data
          const priority = {}
          // eslint-disable-next-line
          res.data.priority.map(item => {
            priority[item.key] = item.doc_count
          })
          // 警报总数
          this.total = res.data.total
          // 各个等级百分比
          // eslint-disable-next-line
          this.priorityPrecent = Object.values(priority).map(item => {
            return Math.round((item / this.total) * 100)
          })
        })
      },
      // 每页条数
      handleSizeChange(val) {
        this.dataList.pagination.size = val
        this.handleSearch()
      },
      // 当前页
      handleCurrentChange(val) {
        this.currentPage = val
        this.dataList.pagination.from = (this.currentPage - 1) * this.dataList.pagination.size
        this.handleSearch()
      },
      // 获取日期选择器时间
      getDateArray() {
        this.isActive = ''
        this.dataList.must[0].from = this.dateArray[0]
        this.dataList.must[0].to = this.dateArray[1]
        this.handleSearch()
      },
      // 根据时间获取数据
      handleTimeData(time, index) {
        this.dataList.must[0].type = 'range'
        this.isActive = index
        this.dataList.must[0].from = this.timeList[time].start
        this.dataList.must[0].to = this.timeList[time].end
        this.handleSearch()
      },
      // 排序
      sortOrder(data) {
        this.dataList.sort = data
      },
      // 回车监听事件
      keyupEnterSearch(event) {
        if (event.keyCode === 13) {
          this.handleSearch()
        }
      },
      openNotify(val) {
        this.$notify({
          title: '提示',
          message: '新增规则:' + val,
          type: 'warning'
        })
      }
    },
    created() {
      setTimeout(this.handleSearch, 50, this.dataList)
      this.handleSearch()
      const socket = io.connect('http://192.168.10.105:8000')
      socket.on('news', (data) => {
        this.openNotify(data.output)
      })
    },
    // 创建监听器
    mounted() {
      window.addEventListener('keyup', this.keyupEnterSearch)
    },
    // 销毁监听器
    beforeDestroy() {
      window.removeEventListener('keyup', this.keyupEnterSearch)
      clearInterval(this.timer)
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .main
    width 95%
    margin 0 auto
    position relative
    top 58px
    .main-nav
      font-size 1.6em
      color #525252
      padding 20px 0 20px 15px
      border-bottom #E5E5E5 solid 1.2px
      a
        color #4470E9
    .main-wrapper
      margin 0 auto
      .brief
        .el-container
          .el-aside
            margin-top 10px
            background-color #FAFAFA
            color #333
            line-height 25px
            padding 9px 0 0 20px
          .icon-shink
            margin-left 314px
            background-color #fafafa
            img
              margin-left 6px
      p
        font-size 1.6em
        color #333333
        font-weight 600
      span
        font-size 14px
        color #4D4D4D
        margin 30px 0 20px 0
        i
          padding 0px 2px 5px 0
      .alertList
        .icon-screen
          color #5286F7
          font-size 1.5em
        button
          line-height 30px
          padding 0 15px
          margin 7.5px
        .el-date-picker
          width 220px
      i
        display: inline-block
      .icon-search
        color #fff
        font-size 1.5em
      .securitylists-text
        margin-bottom: 30px
        .el-select
          width 108px
        p
          display inline-block
          .search-text
            width 300px
            height 30px
            border #5286F7 1px solid
            color #808080
            padding 0 0 0 15px
            font-size 14px
            margin 30px 0 0 0
            display inline-block
          .search-btn
            height 33px
            background #3E7AF6
            border 0px
            font-size 14px
            margin 0px 0 0 -5px
            padding: 6px 35px
            border-radius 0px
      .divblock
        margin-top 20px
        float right

  .history_time
    color #3E7AF6
    background #F4F7FE
    border-color snow

  .history_time_active
    color #FFFFFF
    background #6F97E8
    border-color snow
</style>



